.gallery-wrap {
    background: white;
    
    padding-left: 5vw;
    position: relative;
    overflow: hidden;

    .gallery-heading{
        font-family: "Migra";
        font-weight: 600;
        font-size: 1vw;
        margin-top: 5vw;
        margin-bottom: 5vw;
        text-transform: uppercase;
        text-align: left;

    }

    
    .gallery{
        
        background:white;
        top: 10%;
        max-height: 100vh;
        padding: 5vh;
        width: 400%;
        display: flex;
        flex-direction: nowrap;    
        .gallery-counter {
            position: absolute;
            top: 5%;
            left: 2vw;
            z-index: 1;
            mix-blend-mode: difference;
            line-height: 2vw;
            color: #dbd8d6;
            font-family: "Fraktion Mono";
            font-weight: 600;
            font-size: 1vw;
            -webkit-font-smoothing: antialiased;
            display: inline-block;
    
            .divider{
                content: '';
                background-color: white;
                width: 6.25vw;
                margin: 7px 10px;
                height: 1px;
                display: inline-block;
            }
        }
    }
    .gallery-item{
        padding: 5vw;
        transform: scale(0.5);
        background: white;
        width: 80%;
        height: 80%;
        position: relative;
        will-change: transform;
    }
    .gallery-item-info{
        padding: 5vw;
        padding-top: 2vw;
        padding-top: 1vw;
        color:black;
        text-shadow: 1px 1px black;
        transition: all 1.5s cubic-bezier(0.77, 0, 0.175, 1);
        filter: none;
        position: absolute;
        bottom: 10%;
        z-index: 1;
        background: white;
        border: solid black 0.2vw;
        
        .gallery-item-title {
           
            line-height: 4.7vw;
            font-family: "Migra";
            font-weight: 700;
            font-size: 4vw;
        }
      
        .gallery-item-description {
            text-shadow: 0px 0px;
            color: black;
            position: relative;
            line-height: 1.2;
            font-family: "Fraktion Mono";
            font-weight: 400;
            font-size: 2vw;
           
        }
      
        .gallery-item-category {
            text-shadow: 0px 0px;
            color: black;
            position: relative;
            line-height: 1.2;
            font-family: "Fraktion Mono";
            font-weight: 400;
            font-size: 1.5vw;
            margin-top: 2vh;
           
        }
        .github-link{
            margin-right: 41vw;
            padding-left: 1vw;
            font-size: 1.5vw;
            background-color: black;
            color: white;
            font-weight: 700;
            font-family: "Migra";
            border-radius: 50px;
            cursor: pointer;
            a {
                text-decoration: none;
                color: white;
            }
           
    
           
        }
    }
    .gallery-item-wrapper{
        
        aspect-ratio: 16/9;
        background: white;
        display: grid;
        height: 100%;
        width: 100vw;
        grid-template-columns: 70vw 1fr;
        &.is-reveal{
            .gallery-item {
                transform: scale(0.8);
            }
    }   }

}