@import url('https://fonts.googleapis.com/css2?family=Syncopate:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bai+Jamjuree:wght@400;600;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bodoni+Moda:wght@700;900&display=swap'); //'Bodoni Moda', serif;

@font-face {
    font-family: "Migra";
    src: url("./fonts/Migra/Migra-Extrabold.woff") format("woff");
    font-weight: bold;
}
@font-face {
    font-family: "Fraktion Mono";
    src: url("./fonts/Fraktion/Mono/PPFraktionMono-Regular.otf") format("opentype");
}

@font-face {
    font-family: "Fraktion Mono Bold";
    src: url("./fonts/Fraktion/Mono/PPFraktionMono-Bold.otf") format("opentype");
}
body {
    margin: 0;
    font-size: 0;
    overflow: hidden;
}

.loader-wrapper {
    height: 100vh;
    display: flex;
    background: black;
    flex-direction:column;
    justify-content: center;
    align-items: center;
    color: white;
    font-family: 'Migra';
    font-weight: 700;
    text-align: center;
    font-size: 12vw;

}

